.hero {
  position: relative;
  height: 85vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
  overflow: hidden;
}

#heroVideo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; 
  z-index: 0;
}

.hero-content {
  z-index: 1; 
  position: relative;
  padding: 20px;
}

.hero-content h1 {
  font-size: 3.5em;
  font-weight: bold;
  margin: 0;
  color: #fff;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7); 
}


@media (max-width: 768px) {
  .hero {
      height: 60vh; 
  }

  .hero-content h1 {
      font-size: 2.5em;
  }
}