.section.education {
  padding: 20px;
  text-align: center;
  background-color: #f4f4f4;
  margin: 50px auto;
  max-width: 1000px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.education h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #333;
}

.education div {
  margin-bottom: 30px;
  text-align: left;
}

.education h3 {
  font-size: 1.8em;
  color: #555;
  margin-bottom: 10px;
}

.education p {
  font-size: 1.1em;
  margin-bottom: 5px;
  line-height: 1.6;
  color: #666;
}

.education p strong {
  color: #333;
}

.education p span {
  font-size: 0.9em;
  color: #888;
  display: block;
  margin-top: 5px;
}

.education p em {
  font-style: italic;
  color: #888;
}

/* Responsive layout for smaller screens */
@media (min-width: 768px) {
  .education div {
      text-align: left;
  }
}
