
.section.certifications {
  padding: 20px;
  text-align: center;
  background-color: #f4f4f4;
  margin: 50px auto;
  max-width: 1000px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.certifications h2 {
  font-size: 2.5em;
  margin-bottom: 40px;
  color: #333;
}

.certifications h3 {
  font-size: 2em;
  margin-top: 50px;
  margin-bottom: 20px;
  color: #555;
}


.certifications .cert-container {
  display: inline-block;
  width: 100%;
  max-width: 300px;
  margin: 20px;
  text-align: center;
}

.cert-container img {
  width: 100%;
  max-width: 250px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.cert-container img:hover {
  transform: scale(1.05);
}

.cert-container p {
  margin-top: 10px;
  font-size: 1.2em;
  color: #666;
}

@media (min-width: 768px) {
  .certifications .cert-container {
      max-width: 30%;
  }
}
