nav {
  background: #333;
  color: white;
  padding: 0.25em 0;
  text-align: center;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

nav a {
  font-size: 1.1em;
  color: white;
  margin: 0 15px;
  text-decoration: none;
  transition: color 0.3s ease;
}

nav a:hover {
  color: #ddd;
  text-decoration: underline;
}

html {
  scroll-behavior: smooth;
}

body {
  padding-top: 60px; 
}

.nav-toggle {
  display: none;
  background: none;
  border: none;
  font-size: 2em;
  color: white;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 20px;
}

@media (max-width: 768px) {
  nav {
    padding: 2em 0;
  }

  nav ul {
    display: none;
    flex-direction: column;
    align-items: center;
    background: #333;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
  }

  nav ul.open {
    margin: 0px 0;
    display: flex;
  }

  nav a {
    display: block;
    margin: 10px 0;
  }

  .nav-toggle {
    display: block;
  }
}
